.formulario{
    font-size: 1.4rem;
    color: $grisOscuro;
    margin: 2rem 0 0 0;

    legend{
        font-size: 2rem;
        color: $grisOscuro;
    }

    label{
        font-weight: $bold;
        display: block;
    }
    input:not([type="submit"]),
    textarea,
    select{
        padding: 1rem;
        display: block;
        width: 100%;
        background-color: $blanco;
        border: 1px solid $gris;
        border-radius: 1rem;
        margin-bottom: 2rem;
    }
    .forma-contacto{
        max-width: 30rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    input[type="checkbox"],
    input[type="radio"]{
        margin: 1rem 0;
        width: auto;
    }
}