/** UI **/
.overlay {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background: rgba(0, 0, 0, 0);
  z-index: 0;
}

.loader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #000; /* or any color */
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 5; /* Ensure it covers the video */
}

.loader::before {
  content: "";
  border: 16px solid #f3f3f3; /* Light grey */
  border-top: 16px solid #3498db; /* Blue */
  border-radius: 50%;
  width: 120px;
  height: 120px;
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.header {
  background-color: #0f3973;
  padding: 1rem 0 0 0;
  position: relative;
}
.header .video-container {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  overflow: hidden;
  z-index: 0;
  transition: clip-path 0.5s ease-in-out;
  clip-path: inset(0 0 0 0); /* Initially fully visible */
  display: flex;
  align-items: center;
  justify-content: center;
}
.header .video-container video {
  position: absolute;
  top: 50%;
  left: 50%;
  min-height: 100%;
  width: 103%;
  transform: translate(-50%, -50%);
  object-fit: cover;
  filter: brightness(60%);
}
.header .video-container .titulo-header {
  position: relative;
  z-index: 1;
  text-transform: uppercase;
}
.header .video-container .titulo-header h1 {
  padding: 0 1rem 4rem 1rem;
  text-shadow: 2px 1px 2px rgba(0, 0, 0, 0.52);
  text-align: center;
  color: #FFFFFF;
  font-weight: bold;
  line-height: 2;
  max-width: 100rem;
  font-size: 2.3rem;
  transition: transform 0.5s ease-in-out;
}
@media (min-width: 768px) {
  .header .video-container .titulo-header h1 {
    font-weight: 900;
    padding-bottom: 0;
    padding: 0 0 5rem 2rem;
  }
}
@media (min-width: 768px) {
  .header .video-container .titulo-header h1 {
    padding-left: 0;
    font-size: 5rem;
  }
}
.header.inicio {
  background-position: center center;
  background-size: cover;
  background-color: transparent;
  height: 100vh;
  width: 100%;
  z-index: 4;
  top: 0;
