.ghost-div {
  height: 200vh; /* Height to delay scrolling */
  background: transparent; /* Transparent background */
  position: absolute; /* Absolute positioning to control visibility */
  top: 0; /* Start at the top */
  left: 0;
  width: 100%;
  z-index: 1; /* Ensure it's below main content */
  opacity: 0; /* Initially hidden */
  pointer-events: none; /* Make sure it does not interfere with interactions */
  transition: opacity 0.3s ease; /* Smooth transition effect */
}

